@media only screen and (max-width: 960px) {
  body.popup {
    padding-top: 0;
  }
}

/* PAGE STRUCTURE */
#container {
  padding: 0;
  min-height: 100%;
  transition: padding-left 0.3s;
}
body.menu-pinned #container {
  padding-left: 250px;
}
body.menu-pinned.popup #container {
  padding-left: 0;
}
@media only screen and (max-width: 960px) {
  #container, body.menu-pinned #container {
    padding-left: 0;
  }
}
.popup #container {
  padding-left: 0;
}

#content {
  padding: 20px;
}
@media only screen and (max-width: 480px) {
  #content {
    padding: 10px;
  }
}
#content > h1 {
  display: none;
}

#content-main {
  float: left;
  width: 100%;
}
@media only screen and (max-width: 960px) {
  #content-main {
    float: none;
  }
}

#content-related {
  float: right;
  width: 260px;
  position: relative;
  margin-right: -300px;
}
@media only screen and (max-width: 960px) {
  #content-related {
    float: none;
    width: 100%;
    margin-left: 0;
    position: static;
  }
}

#footer {
  clear: both;
  padding: 10px;
}
#footer:empty {
  display: none;
}

.dialog-confirm {
  display: none;
}

/* COLUMN TYPES */
.colMS {
  margin-right: 300px;
}
@media only screen and (max-width: 960px) {
  .colMS {
    margin-right: 0;
  }
}

.colSM {
  margin-left: 300px;
}
@media only screen and (max-width: 960px) {
  .colSM {
    margin-left: 0;
  }
}

.colSM #content-related {
  float: left;
  margin-right: 0;
  margin-left: -300px;
}
@media only screen and (max-width: 960px) {
  .colSM #content-related {
    float: none;
    margin-left: 0;
  }
}

.colSM #content-main {
  float: right;
}
@media only screen and (max-width: 960px) {
  .colSM #content-main {
    float: none;
  }
}

.popup .colM {
  width: auto;
}